
import Overlay from "./components/overlay.mjs";
import { LoremIpsum } from "lorem-ipsum";
import { createElement } from "./helpers/utils.mjs";




const
    elem = createElement('div', { class: 'overlay', hidden: true }, `<div class="corner pos-br"><a href="https://github.com/aangerformapro/initiation-sass" target="_blank" rel="noopener noreferrer"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 480 512" fill="currentColor"><path d="M186.1 328.7c0 20.9-10.9 55.1-36.7 55.1s-36.7-34.2-36.7-55.1 10.9-55.1 36.7-55.1 36.7 34.2 36.7 55.1zM480 278.2c0 31.9-3.2 65.7-17.5 95-37.9 76.6-142.1 74.8-216.7 74.8-75.8 0-186.2 2.7-225.6-74.8-14.6-29-20.2-63.1-20.2-95 0-41.9 13.9-81.5 41.5-113.6-5.2-15.8-7.7-32.4-7.7-48.8 0-21.5 4.9-32.3 14.6-51.8 45.3 0 74.3 9 108.8 36 29-6.9 58.8-10 88.7-10 27 0 54.2 2.9 80.4 9.2 34-26.7 63-35.2 107.8-35.2 9.8 19.5 14.6 30.3 14.6 51.8 0 16.4-2.6 32.7-7.7 48.2 27.5 32.4 39 72.3 39 114.2zm-64.3 50.5c0-43.9-26.7-82.6-73.5-82.6-18.9 0-37 3.4-56 6-14.9 2.3-29.8 3.2-45.1 3.2-15.2 0-30.1-.9-45.1-3.2-18.7-2.6-37-6-56-6-46.8 0-73.5 38.7-73.5 82.6 0 87.8 80.4 101.3 150.4 101.3h48.2c70.3 0 150.6-13.4 150.6-101.3zm-82.6-55.1c-25.8 0-36.7 34.2-36.7 55.1s10.9 55.1 36.7 55.1 36.7-34.2 36.7-55.1-10.9-55.1-36.7-55.1z" /></svg></a></div>`),
    overlay = new Overlay(elem), delay = {
        show: 0,
        hide: 1.05,
    }, { body } = document, lorem = new LoremIpsum({
        sentencesPerParagraph: {
            min: 1,
            max: 1,
        },
        wordsPerSentence: {
            min: 8,
            max: 8,
        }
    }), related = body.querySelector('.related'), card = body.querySelector('.card').cloneNode(true);


body.appendChild(elem);
let lock = true, cnt = 3, max = 99;

overlay
    .on('hide', () => {

        body.classList.add('menu-closing');
    })
    .on('close', () => {
        body.classList.remove('menu-closing', 'menu-shown');
    })
    .on('show', () => {
        body.classList.remove('menu-closing');
        body.classList.add('menu-showing');
    })
    .on('open', () => {
        body.classList.remove('menu-showing');
        body.classList.add('menu-shown');
    });




addEventListener('click', e => {
    if (e.target.closest('.nav-btn')) {
        e.preventDefault();
        overlay.open ? overlay.hide(delay.hide) : overlay.show(delay.show);
    } else if (e.target.closest('.menu-shown nav a, .menu-shown .logo')) {
        overlay.hide(delay.hide);
    } else if (e.target.closest('.btn-pink')) {
        lock = false;
    }
});


addEventListener("scroll", () => {

    const endOfPage = window.innerHeight + window.pageYOffset >= document.body.offsetHeight - 10;

    if (endOfPage && !lock && related && card) {
        lock = true;

        for (let i = 0; i < 3; i++) {

            if (cnt === max) {
                break;
            }

            cnt++;

            let elem = card.cloneNode(true),
                text = lorem.generateParagraphs(1),
                title = text.split(' ', 3).join(' '),
                cntText = '0' + cnt;

            if (cntText.length > 2) {
                cntText = cntText.slice(1);
            }

            elem.querySelector('.card-text').innerHTML = text;
            elem.querySelector('.card-header').innerHTML = cntText;
            elem.querySelector('.card-title').innerHTML = title;


            related.appendChild(elem);

        }

        setTimeout(() => {
            lock = cnt > max;
        }, 50);


    }

});

addEventListener('load', () => {
    //lock = false;
});
